
import XButton from 'vux/src/components/x-button/index.vue'

import { getBarMoney } from '@/api/'
export default {
name: 'Deposit',
components: {
XButton
},
data () {
return {
barInfo: {}
}
},
beforeRouteEnter (to, from, next) {
document.title = '我要提现'
next()
},
created () {
getBarMoney({ht_id: this.$route.params.id}).then((res) => {
this.barInfo = res.result
})
}
}



